body {
  color: var(--main-text-color);
  background-color: var(--main-background-color);

  font-family: Roboto, sans-serif;
  font-weight: 500;
}
a {
  color: var(--secondary-text-color);
  text-decoration: none;
  font-weight: inherit;
  font-size: 12px;
}
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}
h2 {
  font-size: 12px;
  color: var(--main-text-color);
}
p {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: var(--main-text-color);
}
img {
  display: block;
  height: auto;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  border-radius: 5px;
}
